<template>
    <div>
        <div class="container mt-3">
            <h3 v-show="transfer_status">发送模式</h3>
            <h3 v-show="!transfer_status">接收模式</h3>
            <button type="submit" class="btn btn-primary" @click="transfer_status = !transfer_status">切换</button>
            <!-- <form> -->
                <div class="mb-3 mt-3">
                    <label for="comment" v-show="transfer_status">请输入发送内容：</label>
                    <label for="comment" v-show="!transfer_status">接收到的内容：</label>
                    <textarea v-show="transfer_status" v-model="send_message" class="form-control" rows="7"></textarea>
                    <textarea v-show="!transfer_status" v-model="this.$store.state.receive_message" class="form-control" rows="7" ></textarea>
                </div>
                <button class="btn btn-primary" v-show="transfer_status" @click="socket_send">提交</button>
                <button class="btn btn-primary" v-show="!transfer_status" @click="clear_receive_message">清除</button>
            <!-- </form> -->
        </div>
    </div>
</template>
<script>
import { mapState,mapActions } from 'vuex';
export default {
    data() {
        return {
            send_message: '{p:,i:,d:}',
            receive_message_temp:'',
            transfer_status: true,
        }
},
computed: {
        ...mapState(['socket','receive_message']),
    },
methods: {
    ...mapActions(['updateSocket','updateReceive_message']),
    socket_send(){
        if(this.socket.readyState==1)
        {
            console.log("发送连接建立")
        this.socket.send(this.send_message);
        }else{
            console.log("发送连接未建立");
        }
    },
    clear_receive_message(){
        // console.log(receive_message);
        this.receive_message_temp='';
        // console.log(this.$store.state.receive_message);
        // console.log(this.receive_message_temp);
        // this.receive_message='';
        this.updateReceive_message(this.receive_message_temp);
    }
},
};
</script>
